<template>
  <div>
    <main>
      <div class="company">
        <div class="container fl-ex">
          <!-- 左部分 -->
          <div class="left">
            <el-form :label-position="labelPosition" :model="formLabelAlign">
              <div style="margin-top:10%;">
                <p style="margin-right:60%; font-size:0.87rem;">发起工会:</p>
                <el-select v-model="value" placeholder="请选择工会" style="width:93%; height:2.5rem;">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </div>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:65%; margin-bottom:-5%;">联系人</p>
                <el-input v-model="formLabelAlign.name" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">单位地址</p>
                <el-input v-model="formLabelAlign.region" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:75%; margin-bottom:-5%;">邮箱</p>
                <el-input v-model="formLabelAlign.type" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">活动地点</p>
                <el-input v-model="formLabelAlign.know" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>

              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">活动主题</p>
                <el-input v-model="formLabelAlign.zhu" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
            </el-form>
          </div>
          <!-- 右部分 -->
          <div class="right">
            <el-form :label-position="labelPosition" :model="formLabelAlign">
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">组织代码</p>
                <el-input v-model="formLabelAlign.state" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:-1%;">
                <p style="margin-right:60%; margin-bottom:-5%;">联系方式</p>
                <el-input v-model="formLabelAlign.add" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <div class="block" style="margin-top:7.6%; ">
                <span class="demonstration" style="margin-right:60%; font-size:0.87rem;">成立日期:</span>
                <el-date-picker
                  v-model="value1"
                  type="date"
                  placeholder="选择日期"
                  style="width:93%; height:2.5rem;"
                ></el-date-picker>
              </div>
              <div class="block" style="margin-top:10%;">
                <span class="demonstration" style="margin-right:60%; font-size:0.87rem;">活动日期:</span>
                <el-date-picker
                  v-model="value2"
                  type="date"
                  placeholder="选择日期"
                  style="width:93%; height:2.5rem;"
                ></el-date-picker>
              </div>
              <el-form-item style="width:100%; height:4.6rem; margin-bottom:0.5%;">
                <p style="margin-right:54%; margin-bottom:-5%; font-size:0.86rem;">活动负责人</p>
                <el-input v-model="formLabelAlign.member" style="width:93%; height:2.5rem;"></el-input>
              </el-form-item>
              <div style="margin-top:5%; margin-top:9%;">
                <p style="margin-right:60%; font-size:0.87rem;">附件内容</p>
                <div class="fl-ex">
                  <el-form-item>
                    <el-input v-model="formLabelAlign.book" style="width:90%; height:2.5rem;"></el-input>
                  </el-form-item>
                  <el-upload
                    class="upload-demo"
                    action="https://jsonplaceholder.typicode.com/posts/"
                  >
                    <el-button size="small" style="width:90%; height:2.5rem;">上传文件</el-button>
                  </el-upload>
                </div>
              </div>
            </el-form>
          </div>
        </div>
        <div class="bottom">
          <div style>
            <p style="margin-right:74%; font-size:0.95rem;">活动内容:</p>
            <el-input
              type="textarea"
              placeholder="请输入活动内容"
              v-model="textarea"
              maxlength="250"
              show-word-limit
              style="width:96%; height:3rem;"
            ></el-input>
          </div>
          <div style="width:100%; height:5rem; text-align:center; line-height:3rem; margin-top:3%;">
            <span style="margin-left:-75%; display:block; height:2rem;font-size:0.9rem;">活动评价:</span>
            <el-radio-group v-model="radio">
              <el-radio :label="3">满意</el-radio>
              <el-radio :label="6">良好</el-radio>
              <el-radio :label="9">一般</el-radio>
              <el-radio :label="2">不满意</el-radio>
            </el-radio-group>
          </div>
        </div>
        <!-- 按钮 -->
        <el-row style="margin-top:1.2rem;">
          <el-button type="primary" style="width:21.5%; height:2.2rem;">保存</el-button>
          <el-button type="success" style="width:21.5%; height:2.2rem;">提交</el-button>
        </el-row>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 单选择框
      labelPosition: "top",
      formLabelAlign: {
        name: "",
        region: "",
        type: "",
        know: "",
        book: "",
        zhu: "",
        card: "",
        tel: "",
        state: "",
        add: "",
        member: "",
        duty: "",
        honor: ""
      },
      value1: "",
      value2: "",
      // 下拉选择框
        options: [{
          value: '数据一',
          label: '数据二'
        }],
        value: '',
      value1: "",
      // 日期
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      textarea: "",
      radio: ""
    };
  }
};
</script>

<style scoped>
main {
  display: flex;
  flex-direction: column;
  overflow: auto;
  flex: 1;
}
main .company {
  width: 100%;
  height: 44rem;
  background-color: #e2e2e2;
}
main .company .container {
  width: 96%;
  height: 28rem;
  margin: 0 auto;
  background-color: white;
  margin-top: 2%;
}
main .company .left {
  width: 50%;
  height: 28rem;
}
main .company .right {
  width: 50%;
  height: 28rem;
}
main .company .bottom {
  width: 96%;
  height: 11rem;
  margin: 0 auto;
  background-color: white;
}

/* 
 */
</style>


